<template>
  <section class="hero main-container">
    <div class="movie-theatre-wrapper">
      <p
        class="title is-4 is-size-5-mobile has-text-white has-text-weight-bold"
      >
        Register
      </p>
      <form action="index.html">
        <div class="input-field">
          <input type="email" id="email" v-model="email" />
          <label class="has-text-white" for="email">Email Address</label>
        </div>
        <div class="input-field">
          <input type="password" id="password" v-model="password" />
          <label class="has-text-white" for="email">Password</label>
        </div>
        <button
          v-on:click="register"
          class="btn btn-large btn-extended grey lighten-4 black-text"
        >
          Register
        </button>
      </form>
    </div>
  </section>
</template>

<script>
import "bulma/css/bulma.css";
import firebase from "firebase";

export default {
  name: "Register",
  data() {
    return {
      email: "",
      password: "",
    };
  },
  methods: {
    register(e) {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(
          (user) => {
            alert(`account created for ${user.user.email}`);
            this.$router.push("/");
          },
          (error) => alert(error)
        );
      e.preventDefault();
    },
  },
};
</script>

<style lang="sass" scoped></style>
